import React from "react";
import { observer } from "mobx-react";
import { WeaDialog } from "ecCom";
import MessageCenter from "../message-center/index";
import { Popover, Button } from "antd";
import * as API from "../../apis/index";
import WeaMessageCenterStore from "../../stores/index";

@observer
export default class DialogComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      count: 0,
      popVisiable: false,
      popList: [],
      popIndex: 0,
      popItem: null,
      popEnter: false
    };
    this.timer = null;
  }

  componentDidMount() {
    this.startMsgCount();
    this.getMsgTypeNumberList();
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.intervalTime !== this.props.intervalTime) {
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.getMsgCount();
      }, nextProps.intervalTime);
    }
  }

  startMsgCount() {
    this.getMsgCount();
    clearInterval(this.timer);
    this.timer = setInterval(() => {
      this.getMsgCount();
    }, this.props.intervalTime);
  }

  getMsgTypeNumberList() {
    API.getMsgTypeNumberList().then(res => {
      if (res.item && res.item.length > 0) {
        const newArray = [];
        res.item.forEach(item => {
          if (Number(item.count) > 0) {
            newArray.unshift(item);
          } else {
            newArray.push(item);
          }
        });
        this.setState({
          popList: newArray
        });
      }
    });
  }

  getMsgCount(params, cb) {
    API.getMsgCount(params).then(res => {
      let count = 0;
      if (res && res.count > 0) {
        if (res.count > 99) {
          count = 99;
        } else {
          count = res.count;
        }
      }
      this.setState({ count });
      if (cb && typeof cb === "function") {
        cb(count);
      }
    });
  }

  closeDialog = () => {
    window._message_center = { open: false };
    // $(`#pop-close-${this.props.id}`).show();
    this.setState({
      visible: false
    });
    this.startMsgCount();
  };

  smallBellClick() {
    $(`#pop-close-${this.props.id}`).hide();
    if (Number(this.state.count) > 0) {
      this.setState({
        popVisiable: false
      });
      clearInterval(this.timer);
    }
    window._message_center = { open: true };
    this.setState({
      visible: true,
      popItem: null,
      popIndex: 0
    });
  }

  popUpBellMask() {
    this.getMsgCount({}, count => {
      if (Number(count) > 0) {
        this.setState({ popVisiable: true });
        this.getMsgTypeNumberList();
      }
    });
  }

  checkMore(item = { id: 0 }) {
    this.setState({
      popVisiable: false,
      visible: true,
      popIndex: item.id,
      popItem: item.id === 0 ? null : item
    });
  }

  renderPopContent() {
    return (
      <div
        className="wea-messageCenter-remind-popover-content"
        onMouseEnter={() => {
          this.setState({
            popEnter: true
          });
        }}
        onMouseLeave={() => {
          this.setState({ popVisiable: false, popEnter: false });
        }}
      >
        {/* <div style={{ height: 55 }} /> */}
        <h3 className="title">消息中心</h3>
        <div className="content-container">
          {this.state.popList.map(item => {
            if (item.count === 0) {
              return false;
            }
            return (
              <div
                onClick={() => {
                  this.checkMore(item);
                }}
                className="item"
              >
                <i
                  className={`${item.icon} icon`}
                  style={{
                    background: item.iconColor ? item.iconColor : "#0079de"
                  }}
                />
                <span className="item-text">{item.name}</span>
                <span className="icon-red">
                  {Number(item.count) > 99 ? "99" : item.count}
                </span>
              </div>
            );
          })}
        </div>
        <div
          className="button-more"
          onClick={() => {
            this.checkMore();
          }}
        >
          查看更多
        </div>
      </div>
    );
  }

  render() {
    return (
      <div
        {...this.props}
        id={`dialogMode-message-center-${this.props.id}`}
        onClick={() => {
          this.smallBellClick();
        }}
        onMouseEnter={() => {
          this.popUpBellMask();
        }}
        onMouseLeave={() => {
          setTimeout(() => {
            if (!this.state.popEnter) {
              this.setState({ popVisiable: false });
            }
          }, 500);
        }}
      >
        {/* {this.props.children} */}
        <Popover
          placement="leftBottom"
          visible={this.state.popVisiable}
          content={this.renderPopContent()}
          overlayClassName="wea-messageCenter-remind-popover"
        >
          <span
            style={{ ...this.props.iconStyle }}
            className="wea-messageCenter-remind small-bell-pos"
          >
            <i
              style={
                this.state.count > 0
                  ? { display: "block" }
                  : { display: "none" }
              }
              className="right-corner-num"
            >
              {this.state.count}
            </i>
          </span>
        </Popover>
        <WeaDialog
          title="消息中心"
          closable={false}
          style={{ width: 900 }}
          iconBgcolor="#0079de"
          icon="wea-messageCenter-remind weaTop-remind"
          visible={this.state.visible}
          onCancel={() => {
            this.setState({ visible: false });
          }}
        >
          <MessageCenter
            {...this.props}
            visible={this.state.visible}
            count={this.state.count}
            systemSetStore={WeaMessageCenterStore}
            closeDialog={this.closeDialog}
            getMsgCount={this.getMsgCount.bind(this)}
            popIndex={this.state.popIndex}
            popItem={this.state.popItem}
          />
        </WeaDialog>
      </div>
    );
  }
}



