import React from "react";
import { escape2Html } from "../../util";
import "../../style/index.less";

const bizstateEnum = {
  "1": "已处理",
  "33": "已暂停",
  "34": "已撤销",
  "27": "已删除"
};

class SingleMessage extends React.Component {
  render() {
    return (
      <div className="wea-messageCenter-singleItem">
        <p className="wea-messageCenter-singleItem-time">{this.props.time}</p>
        <div className="wea-messageCenter-singleItem-content">
          <div className="wea-messageCenter-singleItem-title">
            {" "}
            {this.props.name}{" "}
          </div>
          <div
            dangerouslySetInnerHTML={{
              __html: escape2Html(this.props.title)
            }}
            title={escape2Html(this.props.title)}
            className="wea-messageCenter-singleItem-detail"
          />
          <div className="wea-messageCenter-singleItem-creator">
            <p
              dangerouslySetInnerHTML={{
                __html: escape2Html(
                  this.props.context.replace(/<img[^>]+>/gi, "[图片]")
                )
              }}
            />
          </div>
          {Object.keys(bizstateEnum).includes(this.props.bizstate) && (
            <div className="wea-messageCenter-singleItem-handled-tag">
              <i>{bizstateEnum[this.props.bizstate]}</i>
            </div>
          )}
          <a
            // target="_blank"
            className="wea-messageCenter-singleItem-a"
            // href={`${this.props.link}`}
            onClick={() => {
              if (window.em && window.localStorage.emobile_ec_id) {
                window.em.checkJsApi("openLink") &&
                  window.em.openLink({
                    url: this.props.link,
                    openType: 2,
                    sysId: window.localStorage.emobile_ec_id,
                    error: function(error) {
                      alert(JSON.stringify(error));
                    }
                  });
                !window.em.checkJsApi("openLink") &&
                  window.open(this.props.link);
              } else {
                window.open(this.props.link);
              }
            }}
          >
            <div className="wea-messageCenter-singleItem-more">
              阅读详情
              <span className="wea-messageCenter-singleItem-moreIcon">
                &gt;
              </span>
            </div>
          </a>
        </div>
      </div>
    );
  }
}
export default SingleMessage;



