import MessageComponent from "./components/message-center/index";
import MessageStore from "./stores/index";
import FixedPop from "./components/fixedPop";
import DialogComponent from "./components/dialogMode/index";
import SingleMessage from "./components/message-center/singleMessage";

// 高阶函数，用来统一处理异步 config.js
import AsyncHoc from "./components/async-hoc";

let messageComponent = AsyncHoc(MessageComponent, "MessageComponent");
let fixedPop = AsyncHoc(FixedPop, "FixedPop");
let dialogComponent = AsyncHoc(DialogComponent, "DialogComponent");
export default {
  Component: messageComponent,
  DialogComponent: dialogComponent,
  Store: MessageStore,
  FixedPop: fixedPop,
  SingleMessage: SingleMessage
};



