import { observable, action, toJS } from "mobx";
import * as API_TABLE from "../apis/table";
import { Modal, message } from "antd";
import isFunction from "lodash/isFunction";
import uniqueId from "lodash/uniqueId";

export default class TableStore {
  @observable
  dataKey = "";
  @observable
  requireTimes = 0;
  @observable
  loading = false;
  @observable
  datas = [];
  @observable
  columns = [];
  @observable
  operates = [];
  @observable
  sortParams = [];
  @observable
  selectedRowKeys = [];
  @observable
  selectedUniqueIds = [];
  @observable
  showCheck = false;
  @observable
  pageAutoWrap = false;
  @observable
  rowstylefield = "";
  // 分页所有选中 keys
  @observable
  selectedRowKeysAllPages = [];
  // pagination
  @observable
  count = 0;
  @observable
  current = 1;
  @observable
  pageSize = 10;
  // 自定义列
  @observable
  colSetVisible = false;
  @observable
  colSetdatas = [];
  @observable
  colSetKeys = [];
  @observable
  rootMap = undefined;
  @observable
  browser = [];
  @observable
  isSts = false;
  @observable
  stsAllData = undefined;
  @observable
  stsData = undefined;
  @observable
  stsDatas = undefined;
  @observable
  rowCheckKey = []; //当前选中行的key
  checkId = "";
  getDatasCallback = () => {};
  dataHandle = undefined; //处理data
  @observable
  uniqueId = false; //是否使用唯一id
  rowKey = null;

  constructor(props) {
    if (props) {
      props.getDatasCallback &&
        (this.getDatasCallback = props.getDatasCallback);
      props.dataHandle && (this.dataHandle = props.dataHandle);
    }
    this.tableUpdate = this.tableUpdate.bind(this);
    this.getDatas = this.getDatas.bind(this);
    this.setSelectedRowKeys = this.setSelectedRowKeys.bind(this);
    this.tableColSet = this.tableColSet.bind(this);
    this.setColSetVisible = this.setColSetVisible.bind(this);
  }

  @action
  tableUpdate(object, isPending) {
    // isPending 刷新数据进程中清空的选中key 不进行当前页数据的过滤，数据为旧，并非取消当前页选中。
    if (Object.prototype.toString.call(object) === "[object Object]") {
      for (const key in object) {
        if (object.hasOwnProperty(key)) {
          if (key == "datas" && isFunction(this.dataHandle)) {
            this[key] = this.dataHandle(object[key]);
          } else if (
            key === "selectedRowKeys" ||
            key === "selectedRowKeysAllPages"
          ) {
            const sKeys = toJS(this.selectedRowKeys);
            const sKeysAll = toJS(this.selectedRowKeysAllPages);
            const datas = object.datas || (isPending ? [] : toJS(this.datas));
            if (key === "selectedRowKeys") {
              this.selectedRowKeysAllPages = sKeysAll
                .filter(
                  k =>
                    !datas.some(
                      (d, i) =>
                        (this.rowKey ? this.rowKey(d, i) : d.randomFieldId) ===
                        k
                    )
                )
                .concat(object[key]);
              // console.log(toJS(this.selectedRowKeysAllPages));
            }
            if (key === "selectedRowKeysAllPages") {
              this.selectedRowKeys = object[key].filter(k =>
                datas.some(
                  (d, i) =>
                    (this.rowKey ? this.rowKey(d, i) : d.randomFieldId) === k
                )
              );
              // console.log(toJS(this.selectedRowKeys));
            }
            this[key] = object[key];
          } else if (key === "sortParams") {
            if (object[key].length !== 0) {
              this[key] = object[key];
            }
          } else {
            this[key] = object[key];
          }
        }
      }
    }
    return this;
  }
  getDatas(
    sessionkey,
    currentNow,
    pageSizeNow,
    sorter = "",
    callback = this.getDatasCallback,
    clearSelectedRowKeysAllPages = true
  ) {
    const requireTimes = this.requireTimes;
    // 初始化table && 清理部分状态
    const allPagesObj = clearSelectedRowKeysAllPages
      ? { selectedRowKeysAllPages: [] }
      : {};
    this.tableUpdate(
      {
        loading: true,
        selectedRowKeys: [],
        requireTimes: requireTimes + 1,
        ...allPagesObj
      },
      true
    );
    // 已初始化
    const dataKey = sessionkey || this.dataKey;
    const pageSizeChange = pageSizeNow && pageSizeNow != this.pageSize;
    const pageSize = pageSizeNow || this.pageSize;
    const current = pageSizeChange ? 1 : currentNow || this.current;
    const sortParams =
      sorter && sorter.column
        ? [{ orderkey: sorter.column.orderkey, sortOrder: sorter.order }]
        : Array.isArray(sorter)
        ? sorter
        : [];
    if (sorter !== "") {
      this.sortParams = sortParams;
    }
    this.tableUpdate({
      dataKey,
      current,
      pageSize
      // datas: []
    });
    const doGetAPI = () => {
      API_TABLE.getTableDatas({
        dataKey,
        current,
        // sortParams: JSON.stringify(sortParams)
        sortParams: JSON.stringify(toJS(this.sortParams))
      })
        .then(data => {
          const requireTimesNow = this.requireTimes;
          if (data.count === undefined) {
            API_TABLE.getTableCounts({ dataKey }).then(data => {
              const requireTimesNow = this.requireTimes;
              if (
                dataKey == this.dataKey &&
                requireTimesNow === requireTimes + 1
              ) {
                this.tableUpdate({
                  count: data.count
                });
                return data;
              }
            });
          } else {
            this.tableUpdate({
              count: data.count
            });
          }
          if (dataKey == this.dataKey && requireTimesNow === requireTimes + 1) {
            if (this.uniqueId) {
              data.datas = data.datas.map(data => {
                return { ...data, uniqueId: uniqueId(sessionkey) };
              });
            }
            // test
            // data.rootMap.tabletype = "checkbox";
            this.tableUpdate({
              dataKey,
              loading: false,
              datas: data.datas,
              columns: data.columns,
              operates: data.ops,
              showCheck:
                data.rootMap.tabletype == "checkbox" ||
                data.rootMap.tabletype == "radio" ||
                data.haveCheck,
              pageAutoWrap: data.pageAutoWrap,
              rowstylefield: data.rowstylefield,
              // pagination
              pageSize: data.pageSize,
              current,
              sortParams,
              rootMap: data.rootMap,
              browser: data.browser,
              isSts: data.isSts,
              stsAllData: data.stsAllData,
              stsData: data.stsData
            });
            isFunction(callback) && callback();
            return data;
          }
        })
        .then(result => {
          if (result) {
            const { rootMap, ops } = result;
            if (
              rootMap.tabletype == "checkbox" ||
              rootMap.tabletype == "radio" ||
              (ops && ops.length > 0)
            ) {
              const { columns, datas } = result;
              const newDatas = [];
              datas.forEach(data => {
                const newData = {};
                columns.forEach(column => {
                  if (
                    (column.from && column.from === "set") ||
                    column.dataIndex === "randomFieldId"
                  ) {
                    newData[column.dataIndex] = data[column.dataIndex];
                  }
                });
                newDatas.push(newData);
              });
              API_TABLE.getTableChecks({
                dataKey,
                checkId: this.checkId,
                randomDatas: JSON.stringify(newDatas)
              }).then(data => {
                const requireTimesNow = this.requireTimes;
                if (
                  dataKey == this.dataKey &&
                  requireTimesNow === requireTimes + 1
                ) {
                  const resetDatas = datas.map(d => {
                    const _d = { ...d };
                    data.datas &&
                      data.datas.forEach(n => {
                        if (n.randomFieldId === d.randomFieldId) {
                          for (const p in n) {
                            if (n.hasOwnProperty(p)) {
                              _d[p] = n[p];
                            }
                          }
                        }
                      });
                    return _d;
                  });
                  // 根据分页选中 key 需要重置当前页选中 key
                  this.tableUpdate({
                    datas: resetDatas,
                    selectedRowKeysAllPages: toJS(this.selectedRowKeysAllPages)
                  });
                }
              });
            }
          }
        });
    };
    pageSizeChange
      ? API_TABLE.setTablePageSize({
          dataKey,
          pageSize
        }).then(() => {
          doGetAPI();
        })
      : doGetAPI();
    return this;
  }
  reloadCurrent = (callback = this.getDatasCallback) => {
    let { dataKey, current, pageSize, sortParams } = this;
    this.getDatas(dataKey, current, pageSize, toJS(sortParams), callback);
  };
  setSelectedRowKeys(selectedRowKeys = []) {
    if (this.uniqueId) {
      let usedRowKeys = [];
      const datas = toJS(this.datas);
      selectedRowKeys.forEach(key => {
        const data = datas.find(_data => _data.uniqueId === key);
        const index = datas.findIndex(_data => _data.uniqueId === key);
        if (data && index !== -1) {
          usedRowKeys.push(
            this.rowKey ? this.rowKey(data, index) : data.randomFieldId
          );
        }
      });
      this.tableUpdate({
        selectedUniqueIds: selectedRowKeys,
        selectedRowKeys: usedRowKeys
      });
    } else {
      this.tableUpdate({ selectedRowKeys });
    }
    return this;
  }

  setSelectedRowKeysAllPages(selectedRowKeysAllPages = []) {
    this.tableUpdate({ selectedRowKeysAllPages });
    return this;
  }

  // 给使用uniqueId的Table提供的设置选中行的方法。
  setNotUniqueSelectedRowKeys(selectedRowKeys) {
    let uniqueIds = [];
    toJS(this.datas).forEach((data, index) => {
      let rowKey = this.rowKey ? this.rowKey(data, index) : data.randomFieldId;
      if (selectedRowKeys.some(key => key == rowKey)) {
        uniqueIds.push(data.uniqueId);
      }
    });

    this.tableUpdate({
      selectedUniqueIds: uniqueIds,
      selectedRowKeys: selectedRowKeys
    });
  }
  tableColSet(isInit) {
    const { dataKey, colSetKeys } = this;
    const method = isInit ? "GET" : "POST";

    this.tableUpdate({ loading: true });

    API_TABLE.tableColSet(
      isInit
        ? {
            dataKey
          }
        : {
            dataKey,
            systemIds: `${colSetKeys.toJS()}`
          },
      method
    ).then(data => {
      if (data.status) {
        if (data.destdatas) {
          const keys = [];
          data.destdatas.forEach(d => {
            keys.push(d.dataIndex);
          });
          const datas = []
            .concat(data.destdatas)
            .concat(data.srcdatas)
            .filter(d => {
              return !!d.title && d.customCol != "false";
            });
          const newDatas = [];
          datas.map(d =>
            newDatas.push({
              id: d.dataIndex,
              name: d.title,
              description: d.title
            })
          );
          this.tableUpdate({
            colSetKeys: keys,
            colSetdatas: newDatas,
            loading: false
          });
        } else {
          this.tableUpdate({
            colSetVisible: false,
            colSetKeys: [],
            loading: false
          });
          this.getDatas(dataKey);
        }
      } else {
        message.error("接口错误，请重新提交");
      }
    });
    return this;
  }
  setTableColSetkeys(colSetKeys = []) {
    this.tableUpdate({ colSetKeys });
    return this;
  }
  setColSetVisible(colSetVisible = false) {
    this.tableUpdate({ colSetVisible });
    return this;
  }

  readAll = () => {
    this.dataKey;
    this.requireTimes;
    this.loading;
    this.datas;
    this.columns;
    this.operates;
    this.sortParams;
    this.selectedRowKeys;
    this.showCheck;
    this.pageAutoWrap;
    this.rowstylefield;
    // pagination
    this.count;
    this.current;
    this.pageSize;
    // 自定义列
    this.colSetVisible;
    this.colSetdatas;
    this.colSetKeys;
    this.rootMap;
    this.browser;
  };

  exportCurrent = (filetype = "", filename = "") => {
    let min = (this.current < 1 ? 1 : this.current - 1) * this.pageSize + 1,
      max = min + this.pageSize - 1;
    //AndyZhang 添加自定义文件名功能
    if (filename != "") {
      let form = jQuery("<form>");
      form.attr("style", "display:none");
      form.attr("target", "");
      form.attr("method", "post");
      form.attr("action", "/api/ec/dev/table/export");

      let inputmin = jQuery("<input>");
      inputmin.attr("type", "hidden");
      inputmin.attr("name", "min");
      inputmin.attr("value", min);
      form.append(inputmin);

      let inputmax = jQuery("<input>");
      inputmax.attr("type", "hidden");
      inputmax.attr("name", "max");
      inputmax.attr("value", max);
      form.append(inputmax);

      let inputdataKey = jQuery("<input>");
      inputdataKey.attr("type", "hidden");
      inputdataKey.attr("name", "dataKey");
      inputdataKey.attr("value", this.dataKey);
      form.append(inputdataKey);

      let inputfiletype = jQuery("<input>");
      inputfiletype.attr("type", "hidden");
      inputfiletype.attr("name", "filetype");
      inputfiletype.attr("value", filetype);
      form.append(inputfiletype);

      let inputfilename = jQuery("<input>");
      inputfilename.attr("type", "hidden");
      inputfilename.attr("name", "filename");
      inputfilename.attr("value", filename);
      form.append(inputfilename);

      jQuery(document.body).append(form);
      form.submit();
    } else {
      API_TABLE.tableExport({
        dataKey: this.dataKey,
        min,
        max,
        filetype: this.filetype,
        filename: this.filename
      }).then(result => {
        window.location.href = result.url;
      });
    }
  };

  exportAll = (filetype = "", filename = "") => {
    //AndyZhang 添加自定义文件名功能
    if (filename != "") {
      let form = jQuery("<form>");
      form.attr("style", "display:none");
      form.attr("target", "");
      form.attr("method", "post");
      form.attr("action", "/api/ec/dev/table/export");

      let inputdataKey = jQuery("<input>");
      inputdataKey.attr("type", "hidden");
      inputdataKey.attr("name", "dataKey");
      inputdataKey.attr("value", this.dataKey);
      form.append(inputdataKey);

      let inputfiletype = jQuery("<input>");
      inputfiletype.attr("type", "hidden");
      inputfiletype.attr("name", "filetype");
      inputfiletype.attr("value", filetype);
      form.append(inputfiletype);

      let inputfilename = jQuery("<input>");
      inputfilename.attr("type", "hidden");
      inputfilename.attr("name", "filename");
      inputfilename.attr("value", filename);
      form.append(inputfilename);

      jQuery(document.body).append(form);
      form.submit();
    } else {
      API_TABLE.tableExport({ dataKey: this.dataKey, filetype, filename }).then(
        result => {
          window.location.href = result.url;
        }
      );
    }
  };

  setRowCheckKey = (rowCheckKey = []) => {
    this.rowCheckKey = rowCheckKey;
  };
}



