import React from "react";
import { escape2Html } from "../../util";
import SingleMessage from "./singleMessage.js";
import { Icon, message, Button, Card, Spin, Popover } from "antd";
import {
  WeaTab,
  WeaInput,
  WeaTop,
  WeaNewScroll,
  WeaSelect,
  WeaLocaleProvider
} from "ecCom";

import * as API from "../../apis/index";

class MsgListPanel extends React.Component {
  static scroll = null;
  debounce(fn, wait) {
    var last = null;
    const context = this;
    var timer = null;
    return function() {
      const args = arguments;
      clearTimeout(timer);
      var now = new Date();
      if (!last) {
        last = now;
      }
      if (now - last >= wait) {
        fn.apply(context, args);
        last = now;
      } else {
        timer = setTimeout(function() {
          fn.apply(context, args);
        }, wait);
      }
    };
  }

  render() {
    const topTab = [
      {
        color: "#000000",
        title: "待处理",
        viewcondition: 0
      },
      {
        color: "#ff3232",
        title: "全部",
        viewcondition: 1
      }
    ];
    let leftScrollHeight =
      this.props.offsetHeight === 0 ? 525 : 525 + this.props.offsetHeight + 53;
    if (this.props.unReadMsgValue === "block") {
      // leftScrollHeight -= 25;
    }
    if (this.props.tabFlag === "1") {
      leftScrollHeight -= 48;
    }
    return (
      <div className="wea-messageCenter-leftList">
        {/* 顶部点击 */}
        <div
          className="wea-messageCenter-stickyinfo"
          style={{ display: "none" || this.props.unReadMsgValue }}
        >
          <div style={{ float: "left", "margin-right": "10px" }}>
            <a
              onClick={() => {
                this.props.setState({ flag: false });
                this.props.getMsgList(
                  this.props.current,
                  this.props.msgid,
                  this.props.mintime
                );
              }}
            >
              <Icon className="icon" type="exclamation-circle" />
              你还有尚未读的消息,请点击阅读
            </a>
          </div>

          <div style={{ float: "right" }}>
            <a
              onClick={() => {
                API.setMsgRead({ allReadFlag: 1 }).then(res => {
                  if (res) {
                    this.props.getMsgTypeList();
                  }
                });
                this.props.setState({
                  unReadMsgValue: "none"
                });
              }}
            >
              全部置为已读
            </a>
          </div>
        </div>
        {/* 顶部 tips 出现时的 占位符 */}
        {/* {this.state.unReadMsgValue === "block" && (
                <div style={{ height: 25 }} />
              )} */}
        {/* 提醒有该tab */}
        <div
          style={{
            display: this.props.tabFlag === "1" ? "block" : "none"
          }}
          className="sticky-top-tab"
        >
          <WeaTab
            datas={topTab}
            keyParam="viewcondition" //主键
            countParam="groupid" //数量
            selectedKey={this.props.tabSelectedKey}
            onChange={v => {
              this.props.setState({ tabSelectedKey: v, refresh: true }, () => {
                this.props.getMsgList(this.props.current, 0, 0, null, true);
              });
            }}
          />
        </div>
        {this.props.tabFlag === "1" && <div style={{ height: 48 }} />}

        <WeaNewScroll
          ref={e => {
            this.props.scroll(e);
            this.scroll = e;
          }}
          height={leftScrollHeight}
          onScroll={this.debounce(function(e) {
            const position = e.target && e.target.scrollTop;
            this.props.getReadedMsg(position);
          }, 200)}
          // onScroll={e => {
          //   const position = e.target.scrollTop;
          //   this.getReadedMsg(position);
          //   if (e.target.scrollTop === 0 && !this.state.loading) {
          //     this.setState({ flag: false });
          //     this.getMsgList(
          //       this.state.current,
          //       this.state.msgid,
          //       this.state.mintime
          //     );
          //   }
          // }}
        >
          {this.props.msgList.length === 0 && !this.props.loading ? (
            <div className="wea-messageCenter-nomsg">
              <div className="nomsg-container">
                <span style={{ fontSize: 80 }}>
                  <i className="icon-coms-blank" />
                </span>
                <br />
                暂无消息
              </div>
            </div>
          ) : (
            <div className="click-look-more">
              <span>
                {this.props.loading ? (
                  <Icon type="loading" />
                ) : (
                  <React.Fragment>
                    {this.props.newGetMsgList.length > 2 ? (
                      <span
                        onClick={() => {
                          this.props.getMsgList(
                            this.props.current,
                            this.props.msgid,
                            this.props.mintime,
                            null,
                            true,
                            false
                          );
                        }}
                      >
                        <Icon type="clock-circle-o" />
                        <a href="javascript:void(0);"> 点击加载更多 </a>
                      </span>
                    ) : (
                      <span className="no-more">没有更多啦</span>
                    )}
                  </React.Fragment>
                )}
              </span>
            </div>
          )}
          {this.props.msgList.length > 0 &&
            this.props.msgList.map(data => {
              return (
                <SingleMessage
                  {...data}
                  name={data.name || ""}
                  time={data.time || ""}
                  link={data.link || ""}
                  title={data.title || ""}
                  context={data.context || ""}
                />
              );
            })}
        </WeaNewScroll>
        {
          <div
            className="wea-messageCenter-stickyinfo"
            style={{
              display: this.props.newMsgValue
            }}
            onClick={() => {
              this.props.getNewMsgList(
                this.props.current,
                this.props.maxtime,
                1,
                () => {
                  if (this.props.current !== "0") {
                    this.props.setMsgRead();
                  }
                  this.scroll.refs.weaBaron.scrollToLast();
                }
              );
            }}
          >
            <div
              className="bottom-left-title"
              dangerouslySetInnerHTML={{
                __html: escape2Html(
                  this.props.messagetype + " " + this.props.title
                )
              }}
            />
            <div style={{ float: "right" }}>
              <a
                style={{
                  "border-style": "none",
                  cursor: "pointer",
                  "background-color": "	#BBDBF9"
                }}
              >
                {this.props.count}
                条新消息
              </a>
            </div>
          </div>
        }
      </div>
    );
  }
}

export default MsgListPanel;



