import React from "react";
import { Icon, message, Button, Card, Spin, Popover } from "antd";
import {
  WeaTab,
  WeaInput,
  WeaTop,
  WeaNewScroll,
  WeaSelect,
  WeaLocaleProvider
} from "ecCom";

class MsgTypePanel extends React.Component {
  render() {
    const { options, moduleId, viewAttr } = this.props;
    return (
      <div>
        <div className="wea-messageCenter-rightList">
          <div className="search-panel">
            <Popover
              overlayClassName="wea-messageCenter-custom-popover"
              // overlayStyle={{ width: 168 }}
              content={
                <div className="wea-messageCenter-rightList-advancedSearch">
                  <p> 消息种类: </p>
                  <div>
                    <WeaInput
                      value={this.props.keyword}
                      onChange={e => {
                        this.props.setState({
                          keyword: e
                        });
                      }}
                    />
                  </div>
                  <p> 模块: </p>
                  {/* 由于 WeaSelect的设计在父级宽度 -3px， 导致这里只能 148 + 1 = 151 :( */}
                  <div>
                    <WeaSelect
                      style={{ width: 180 }}
                      options={options}
                      value={moduleId}
                      viewAttr={viewAttr}
                      widthMatchOptions={true}
                      onChange={v => {
                        this.props.setState({
                          moduleId: v
                        });
                      }}
                    />
                  </div>
                  <div
                    style={{
                      margin: "18px -10px 0",
                      height: 1,
                      background: "#ebebeb"
                    }}
                  />
                  <div style={{ textAlign: "center", marginBottom: 0 }}>
                    <Button
                      className="advanced-button"
                      onClick={() => this.props.advancedSearch("search")}
                      type="primary"
                    >
                      搜索
                    </Button>
                    &emsp;
                    <Button
                      className="advanced-button"
                      onClick={() => this.props.advancedSearch("reset")}
                    >
                      重置
                    </Button>
                  </div>
                </div>
              }
              placement="bottom"
              visible={this.props.searchVisible}
            >
              <div>
                <WeaInput
                  value={this.props.keyword}
                  placeholder="请输入关键字"
                  onChange={e => {
                    this.props.setState({
                      keyword: e
                    });
                  }}
                  onPressEnter={e => {
                    this.props.advancedSearch("search");
                  }}
                />
                <i
                  onClick={() => {
                    this.props.setState({
                      searchVisible: !this.props.searchVisible
                    });
                  }}
                  className={
                    this.props.keyword !== "" || this.props.moduleId !== ""
                      ? "icon-coms-screen2"
                      : "icon-coms-screen"
                  }
                />
              </div>
            </Popover>
          </div>
          <WeaNewScroll
            height={
              this.props.offsetHeight === 0
                ? 520
                : 520 + this.props.offsetHeight
            }
          >
            <ul>
              {this.props.rightList.length > 0 &&
                this.props.rightList.map(item => {
                  // 暂时不展示 [全部] 标签
                  if (item.id === "0") {
                    return false;
                  }
                  return (
                    <li
                      className={`item ${
                        item.id === this.props.current ? "active" : ""
                      }`}
                      onClick={() => {
                        this.props.setState({
                          flag: true,
                          newMsgValue: "none",
                          current: item.id
                        });
                        this.props.changeRight(item.id, 0, 0, item);
                      }}
                    >
                      <div>
                        <i
                          style={{
                            background: item.iconColor
                              ? item.iconColor
                              : "#0079de"
                          }}
                          className={`icon ${item.icon}`}
                        />
                        <span class="title" title={item.name}>
                          {item.name}
                        </span>
                        {Number(item.count) > 0 && (
                          <span className="num">{item.count}</span>
                        )}
                      </div>
                    </li>
                  );
                })}
            </ul>
          </WeaNewScroll>
        </div>
      </div>
    );
  }
}

export default MsgTypePanel;



