import React from "react";
import { observer } from "mobx-react";
import { Icon, message, Button, Card, Spin, Popover } from "antd";
import {
  WeaTab,
  WeaInput,
  WeaTop,
  WeaNewScroll,
  WeaSelect,
  WeaLocaleProvider
} from "ecCom";

import MsgListPanel from "./msg-list-panel";
import MsgTypePanel from "./msg-type-panel";

import "../../style/index.less";
import SystemSet from "../systemSet";
import * as API from "../../apis/index";
import WeaMessageCenterStore from "../../stores/index";

const getLabel = WeaLocaleProvider.getLabel;
let newMsg = 0;

@observer
class MessageCenter extends React.Component {
  static scroll = null;
  constructor(props) {
    super(props);
    this.state = {
      visible: props.visible,
      options: [],
      value: "",
      viewAttr: 2,
      loading: true,
      current: this.props.popIndex,
      systemSetVisiable: false,
      maxTimeFlag: false,
      rightList: [],
      msgid: "0",
      flag: true,
      maxtime: "0",
      mintime: "0",
      messagetype: "",
      title: "",
      newMsgValue: "none",
      unReadMsgValue: "none",
      msgList: [],
      count: 0,
      unReadCount: 0,
      searchVisible: false,
      keyword: "",
      tabSelectedKey: "0",
      tabFlag: "",
      moduleId: "",
      readedMsgIs: [],
      refresh: true,
      newGetMsgList: []
    };
    this.position = 0;
  }

  componentDidMount() {
    if (this.state.visible) {
      this.getMsgTypeList((current, item) => {
        this.changeRight(current, "0", "0", item);
      });
      this.getModule();
      clearInterval(newMsg);
      newMsg = setInterval(() => {
        this.getNewMsgTypeNumberList();
        this.getNewMsgList(this.state.current, this.state.maxtime, 0);
      }, this.props.intervalTime);
    }
    this.setState({ visible: this.props.visible });
  }

  componentWillUnmount() {
    clearInterval(newMsg);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ visible: nextProps.visible });
    // console.log(this.state.visible !== nextProps.visible);
    if (nextProps.popIndex !== this.props.popIndex) {
      this.setState({ current: nextProps.popIndex });
    }
    if (nextProps.visible) {
      if (this.state.visible !== nextProps.visible) {
        this.setState({ refresh: true }, () => {
          this.getMsgTypeList((current, item) => {
            this.changeRight(current, "0", "0", item);
          });
          this.getModule();
        });
      }
      clearInterval(newMsg);
      newMsg = setInterval(() => {
        this.getNewMsgTypeNumberList();
        this.getNewMsgList(this.state.current, this.state.maxtime, 0);
      }, nextProps.intervalTime);
    }
  }

  changeRight = (index, msgid1, mintime1, item) => {
    // fetch left data
    let tabFlag = "";
    if (item) {
      tabFlag = typeof item.flag === "undefined" ? "" : item.flag;
    }
    this.setState(
      {
        tabFlag,
        refresh: true
      },
      () => {
        this.setMsgRead(() => {
          this.getMsgList(index, msgid1, mintime1, res => {
            this.scroll.refs.weaBaron.scrollToLast();
          });
        });
      }
    );
  };

  getMsgTypeList(cb) {
    const { keyword, moduleId } = this.state;
    API.getMsgTypeNumberList({ groupid: moduleId, keyword }).then(res => {
      if (res.item) {
        const newArray = [];
        res.item.forEach(item => {
          if (Number(item.count) > 0) {
            newArray.unshift(item);
          } else {
            newArray.push(item);
          }
        });
        this.setState({
          rightList: newArray
        });
        //'点击小铃铛定位第一个
        let current = newArray[0] ? newArray[0].id : "0";
        if (Number(this.state.current) === 0) {
          this.setState({
            current
          });
        }
        if (cb && typeof cb === "function") {
          cb(current, newArray[0]);
        }
      }
    });
  }

  getNewMsgTypeNumberList() {
    const { rightList, keyword, moduleId, maxtime } = this.state;
    API.getNewMsgTypeNumberList({ groupid: moduleId, maxtime, keyword }).then(
      res => {
        // res = {
        //   item: [
        //     { name: "全部", count: "1", id: "0" },
        //     { name: "提到我的", count: "2", id: "1,1" },
        //     { name: "关注流程提醒", count: "8", id: "2,9" },
        //     { name: "asdfasdf", count: "1", id: "99,99" }
        //   ],
        //   status: "1"
        // };
        if (res && res.item) {
          const newArray = [];
          for (let i = 0; i < res.item.length; i++) {
            for (let j = 0; j < rightList.length; j++) {
              if (res.item[i].id === rightList[j].id) {
                rightList.splice(j, 1);
              }
            }
          }
          newArray.push(...res.item, ...rightList);
          this.setState({ rightList: newArray });
        }
      }
    );
  }

  setMsgRead = (cb = null) => {
    const newRightList = [];
    const index = this.state.current;
    this.state.rightList.forEach(item => {
      if (item.id === index) {
        item.count = 0;
      }
      newRightList.push(item);
    });
    this.setState({
      rightList: newRightList
    });
    API.setMsgRead({ id: this.state.current }).then(res => {
      // 更新小铃铛数字
      this.props.getMsgCount();
      if (cb && typeof cb === "function") {
        cb();
      }
    });
  };

  getNewMsgList(index, maxtime1, readflag, cb) {
    API.getNewMsgList({
      id: index,
      maxtime: maxtime1,
      flag: readflag
    }).then(res => {
      if (res) {
        if (res.newMessage === 1 && res.data.length > 0) {
          if (this.state.msgList.length === 0) {
            this.setState({
              mintime: res.mintime,
              msgid: res.msgid
            });
          }
          // interval event
          if (readflag === 0) {
            this.setState({
              newMsgValue: "block",
              messagetype: res.data[res.data.length - 1].name,
              count: res.data.length,
              title: res.data[res.data.length - 1].title
            });
          } else {
            // onClick event
            let newMsgList = [...this.state.msgList, ...res.data];
            this.setState(
              {
                newMsgValue: "none",
                msgList: newMsgList,
                maxtime: res.maxtime
              },
              () => {
                if (cb) {
                  cb();
                }
              }
            );
            // setTimeout(() => {
            // this.scroll.refs.weaBaron.scrollToLast();
            // }, 0);
          }
        } else {
          this.setState({
            newMsgValue: "none"
          });
        }
      } else {
        this.setState({
          newMsgValue: "none"
        });
      }
    });
  }

  getMsgList(
    index,
    msgid1,
    mintime1,
    cb,
    scroll = false,
    isUpdateMaxTime = true
  ) {
    let bizstate = "";
    if (this.state.tabFlag === "1") {
      bizstate = "0";
      if (this.state.tabSelectedKey === "1") {
        bizstate = "";
      }
    }
    this.setState({ loading: true });

    API.getMsgList({
      id: index,
      pagesize: 20,
      msgid: msgid1,
      mintime: mintime1,
      bizstate
    }).then(res => {
      // res.data = [
      //   {
      //     name: "超时流程",
      //     time: Math.random()
      //       .toString(36)
      //       .substr(2, 5),
      //     title:
      //       "「项目管理」流程即将超时：【1028】门户元素显示字段中的标题列自适应宽度改造（预计完成日期：2016-02-29",
      //     context:
      //       "创建人：罗燕 <br/> 创建时间：2018-04-13   10:34:23大家好： 麻烦大家反馈一下公司系统访问速度情况，特别是外地机构同事或者上海同事在外面演示的时候，是否感觉打开公司系统很慢，或者打开某些流程模块很慢，当你处于…",
      //     link: ""
      //   },
      //   {
      //     name: "超时流程",
      //     time: Math.random()
      //       .toString(36)
      //       .substr(2, 5),
      //     title:
      //       "「项目管理」流程即将超时：【1028】门户元素显示字段中的标题列自适应宽度改造（预计完成日期：2016-02-29",
      //     context: "创建人：罗燕 <br/> 创建时间：2018-04-13   10:34:23",
      //     link: ""
      //   },
      //   {
      //     name: "超时流程",
      //     time: Math.random()
      //       .toString(36)
      //       .substr(2, 5),
      //     title:
      //       "「项目管理」流程即将超时：【1028】门户元素显示字段中的标题列自适应宽度改造（预计完成日期：2016-02-29",
      //     context: "创建人：罗燕 <br/> 创建时间：2018-04-13   10:34:23",
      //     link: ""
      //   }
      // ];
      // res.data = [];
      if (res.data) {
        let msgList = [];

        if (this.state.refresh) {
          msgList = res.data.length > 0 ? res.data : [];
        } else {
          msgList = [...res.data, ...this.state.msgList];
        }
        setTimeout(() => {
          this.setState({ loading: false });
        }, 300);
        if (scroll) {
          // console.log(this.scroll.refs);
          window.scroll = this.scroll;
          window.scroll.refs.weaBaron.getScroller().scrollTop =
            res.data.length * 200;
          // this.scroll.refs.weaBaron.scroll(4000);
        }
        this.setState({
          flag: true,
          mintime: res.mintime,
          msgid: res.msgid,
          refresh: false,
          newGetMsgList: res.data,
          msgList
        });
        if (isUpdateMaxTime) {
          this.setState({
            maxtime: res.maxtime
          });
        }
        if (cb && typeof cb === "function") {
          cb(res);
        }
        // this.getMsgCount(this.state.mintime);
      }
    });
  }
  getModule() {
    API.getModule().then(res => {
      if (res && res.options) {
        const newArray = res.options.map(item => {
          if (item.showname === "") {
            return Object.assign({}, item, { showname: "全部" });
          }
          return item;
        });
        this.setState({
          options: newArray
        });
      }
    });
  }

  getMsgCount(mintime1) {
    API.getMsgCount({ mintime: mintime1 }).then(res => {
      if (res.count === 0) {
        this.setState({
          unReadCount: res.count,
          unReadMsgValue: "none"
        });
      } else
        this.setState({
          unReadCount: res.count,
          unReadMsgValue: "block"
        });
    });
  }

  advancedSearch(type, v) {
    switch (type) {
      default:
        break;
      case "search":
        break;
      case "reset":
        this.setState({
          keyword: "",
          moduleId: "",
          tabFlag: ""
        });
        break;
    }
    this.setState(
      {
        searchVisible: false,
        refresh: true
      },
      () => {
        this.getMsgTypeList();
        // this.getMsgList(0, 0, 0);
      }
    );
  }

  getReadedMsg(position, itemHeight = 200) {
    // console.log(position, this.position);
    const msgList = this.state.msgList;
    let unReadIndex = Math.floor(position / itemHeight) + 1;
    if (position < itemHeight) {
      unReadIndex = 0;
    }
    const temp = [];
    for (let i = unReadIndex; i < msgList.length; i++) {
      const readedItemId = msgList[i].messageid;
      temp.push(readedItemId);
    }
    if (temp.length > this.state.readedMsgIs.length) {
      // 已读调用暂时取消
      // API.setMsgRead({ msgid: temp.join(",") });
      this.setState({
        readedMsgIs: temp
      });
    }
    if (position) {
      // console.log(position < itemHeight && position - this.position < 0);
      if (position < itemHeight / 2 && position - this.position < 0) {
        this.setState({ flag: false });
        this.getMsgList(
          this.state.current,
          this.state.msgid,
          this.state.mintime,
          null,
          true,
          false
        );
      }
      this.position = position;
    }
  }

  render() {
    const { options, viewAttr, current, moduleId } = this.state;
    const offsetHeight = window.screen.height > 800 ? 0 : -80;
    return (
      <div ref={"__refs_messagecenter"}>
        <div className="wea-messageCenter-container">
          <div className="wea-messageCenter-topbar">
            <WeaTop
              buttons={[
                <i
                  onClick={() => {
                    this.setState({
                      systemSetVisiable: true
                    });
                  }}
                  className="icon-coms-Flow-setting right-icon"
                />,
                <Icon
                  className="right-icon"
                  onClick={() => {
                    typeof this.props.closeDialog === "function" &&
                      this.props.closeDialog();
                  }}
                  type="cross"
                />
              ]}
            />
          </div>
          <div
            style={offsetHeight === 0 ? {} : { height: 600 + offsetHeight }}
            className="wea-messageCenter-content"
          >
            <MsgTypePanel
              offsetHeight={offsetHeight}
              options={options}
              moduleId={this.state.moduleId}
              viewAttr={this.state.viewAttr}
              setState={(obj, cb) => {
                this.setState(obj, () => {
                  if (cb) {
                    cb();
                  }
                });
              }}
              keyword={this.state.keyword}
              searchVisible={this.state.searchVisible}
              rightList={this.state.rightList}
              current={this.state.current}
              changeRight={(a, b, c, d) => {
                this.changeRight(a, b, c, d);
              }}
              advancedSearch={(a, b) => {
                this.advancedSearch(a, b);
              }}
            />
            <MsgListPanel
              offsetHeight={offsetHeight}
              setState={(obj, cb) => {
                this.setState(obj, () => {
                  if (cb) {
                    cb();
                  }
                });
              }}
              unReadMsgValue={this.state.unReadMsgValue}
              tabFlag={this.state.tabFlag}
              tabSelectedKey={this.state.tabSelectedKey}
              msgList={this.state.msgList}
              loading={this.state.loading}
              newMsgValue={this.state.newMsgValue}
              messagetype={this.state.messagetype}
              newGetMsgList={this.state.newGetMsgList}
              title={this.state.title}
              count={this.state.count}
              current={this.state.current}
              msgid={this.state.msgid}
              mintime={this.state.mintime}
              scroll={e => {
                this.scroll = e;
              }}
              getReadedMsg={params => {
                this.getReadedMsg(params);
              }}
              getMsgList={(a, b, c, d) => {
                this.getMsgList(a, b, c, d);
              }}
              getNewMsgList={(a, b, c, d) => {
                this.getNewMsgList(a, b, c, d);
              }}
              getMsgTypeList={() => {
                this.getMsgTypeList();
              }}
              setMsgRead={a => {
                this.setMsgRead(a);
              }}
            />
          </div>
        </div>
        <SystemSet
          // systemSetStore={this.props.systemSetStore}
          getMsgTypeList={this.getMsgTypeList.bind(this)}
          systemSetStore={WeaMessageCenterStore}
          visible={this.state.systemSetVisiable}
          onCancel={() => {
            this.setState({
              systemSetVisiable: false
            });
          }}
        />
      </div>
    );
  }
}

export default MessageCenter;



